/* eslint-disable */
import React from "react";
import { Link as GatsbyLink } from 'gatsby-plugin-intl';
import { getClassName } from "tools/getClassName";

import './Link.scss';

const cn = getClassName('Link');

type Props = {
    to: string;
    children: React.ReactNode;
    className?: string;
    partiallyActive?: boolean;
    active?: boolean;
}

export const Link = ({
  children,
  to,
  className = '',
  partiallyActive,
  ...other
}: Props) => {
  const internal = /^\/(?!\/)/.test(to);

  if (internal) {
    return (
      <GatsbyLink
        to={to}
        activeClassName={cn({ active: true })}
        className={cn(null, className)}
        partiallyActive={partiallyActive}
      >
        {children}
      </GatsbyLink>
    );
  }
  return (
    <a className={cn(null, className)} href={to} {...other}>
      {children}
    </a>
  );
};

export default Link;
