import React from 'react';
import { Link } from 'components/Link/Link';
import { Text } from 'components/Text/Text';

import { getClassName } from 'tools/getClassName';

import './FooterContact.scss';

const cn = getClassName('footer-contact');

type Props = {
    label: string,
    text: string,
    link: string,
    className?: string,
}

export const FooterContact = ({
  label,
  text,
  link,
  className = '',
}:Props) => (
  <div className={cn(null, className)} key={link}>
    {/* <Text as='span' color='footer' className={cn('label')}>{label}</Text> */}
    <Link to={link} className={cn('link')}>
      <Text translate={false} color='white'>{text}</Text>
    </Link>
  </div>);
