import { useMemo } from 'react';
import { useIntl } from "gatsby-plugin-intl";
import { capitalizeFirstLetter } from 'tools/capitalizeFirst';

export const useLocalizedText = ({
  id,
  children,
  capitilize,
  translate = true,
}: {
  id?: string;
  children: string;
  capitilize?: boolean;
  translate?: boolean;
}) => {
  const intl = useIntl();
  const text = useMemo(() => {
    const textValue = id || children;
    const message = translate ? intl.formatMessage({ id: textValue }) : textValue;

    return capitilize ? capitalizeFirstLetter(message) : message;
  },
  [id, children, capitilize]);

  return text;
};
