import React, { useMemo } from 'react';
import { getClassName } from 'tools/getClassName';
import { FONT_COLORS, TFontColors } from "../../colors.const";
import { useLocalizedText } from 'hooks/useLocalizedText';

import './Text.scss';

export type Props = CommonTextProps & {
  as?: 'p' | 'span' | 'div',
  color: TFontColors,
  className?: string,

}

export type CommonTextProps = {
  capitilize?: boolean,
  translate?: boolean,
  id?: string,
  children: string,
}

const cn = getClassName('text');

export const Text = ({ 
  as,
  color,
  className,
  ...textProps
}: Props) => {
  const Component = as || 'p';

  const style = useMemo(() => ({ color: FONT_COLORS[color] }), [color]);
  const text = useLocalizedText(textProps);

  return (
    <Component
      style={style}
      className={cn(null, className)}
    >
      {text}
    </Component>
  );
};
