import React, {
  useCallback, useState 
} from 'react';
import {
  changeLocale, useIntl 
} from 'gatsby-plugin-intl';

import { capitalizeFirstLetter } from 'tools/capitalizeFirst';

import { getClassName } from 'tools/getClassName';

const cn = getClassName('LanguageSelect');

type Props = {
    className?: string;
    language: string;
    onChange?: (locale: string) => void;
}

const options = [
  {
    id: 'en',
    label: 'English' 
  },
  {
    id: 'ru',
    label: 'Russian' 
  },
  {
    id: 'ca',
    label: 'Catalan' 
  },
  {
    id: 'es',
    label: 'Spanish' 
  },
];

export const LanguageSelect = (props: Props) => {
  const [locale, setLocale] = useState(props.language);
  const intl = useIntl();

  const onChange = useCallback((e: React.ChangeEvent<HTMLSelectElement>) => {
    const locale = e.target.value;

    setLocale(locale);
    changeLocale(locale);
    props.onChange?.(locale);
  }, []);

  return (
    <select
      value={locale}
      onChange={onChange}
      className={cn(null, props.className)}
    >
      {options.map(({ id, label }) => {
        const tranlsatedLabel = intl.formatMessage({ id: label });

        return (
          <option key={id} value={id}>
            {capitalizeFirstLetter(tranlsatedLabel)}
          </option>
        );
      })}
    </select>
  );
};
